@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  color:  #333;
}

.logo {
  height: 70px;
}


#menu {
  background-color: #fff;
  display: flex;
  align-items: center;
  padding: 5px 30px;
  box-shadow: 0px 1px 5px -1px rgba(0, 0, 0, 0.75);
  justify-content: space-between;
  position: fixed;
  width: 100%;
  top: 0;
}


#menu a {
  color:  #333;
  margin-right: 50px;
  text-decoration: none;
  font-size: 21px;
  transition: 180ms;
}

#menu a:hover {
  text-decoration: underline;
}

#menu a:active {
  color: #a9a9a9;
  font-size: 20px;
}

#container {
  margin: 120px auto;
  width: 980px;
}

.title{
  color: #410ff8;
}

.tabla {
  width: 100%;
  text-align: left;
}
.tabla th{
  background-color:#dbd1fe;
  text-align: center;
  padding:10px 0;
}

.tabla tr:nth-child(even){
  background-color: #dbd1fe;
}

.tabla td {
  padding: 10px;
}

.icon{
  width: 30px;
  margin: auto;
  display: block;
  cursor: pointer;
}

.center {
  text-align: center;
}

.rojo {
  border-radius: 15px;
  padding: 15px;
  color: red;
  background-color: #F8E0E0;
  border: 1px solid red;
  width: 500px;
  margin: auto;
}

.pub_title {
  border-top: 1px solid black;
  cursor: pointer;
}

li {
  margin: 15px 0;
}

.task_container {
  font-size: 20px;
  margin: 0 0 70px 50px;
}

.m_left {
  margin-left: 20px;
}

p{
  color:  #333;
  font-size: 18px;
  
}

.post{
  border:1px solid #EDEDED;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 3px;
  margin-bottom: 21px;
  background-color: #F9F9F9;
  line-height: 1.6em;
  cursor: pointer;
  
}

.button{
  background-color: #410ff8;
  color: white;
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 16px;
  font-weight: bold;
}

.form label{
  font-weight: bold;
  width: 100px;
  display: block;
  margin-bottom: 10px;
}

.form input{
  height: 30px;
  font-size: 16px;
  width: 300px;
  padding: 0 5px;
}
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #253a46;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@-webkit-keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}

.task_row{
    display: flex;
    padding: 10px;
}

.task_row:nth-child(even){
    background-color: #dbd1fe;
}
.task_col1{
    width: 35px;
}
.task_col2{
    width: 100%;
}
.task_col3, .task_col4{
    width: 70px;
}
